/* NEw */

.section-team {
    font-family: "Poppins", sans-serif;
    padding: 80px 0;
}

.section-team .header-section {
    margin-bottom: 50px;
}

.section-team .header-section .small-title {
    margin-bottom: 25px;
    font-size: 16px;
    font-weight: 500;
    color: #3e64ff;
}

.section-team .header-section .title {
    font-weight: 700;
    font-size: 45px;
}

.section-team .single-person {
    margin-top: 30px;
    padding: 30px;
    background-color: #f6f9ff;
    border-radius: 5px;
    background: linear-gradient(to right, #016cec, #00b5f7);
}

.section-team .single-person:hover {
    background: linear-gradient(to right, #016cec, #00b5f7);
}

.section-team .single-person .person-image {
    position: relative;
    margin-bottom: 50px;
    border-radius: 50%;
    border: 4px dashed transparent;
    transition: padding .3s;
    padding: 12px;
    border: 4px dashed #fff;
}

.speciality {
    text-transform: uppercase;
    font-size: 14px;
    color: #016cec;
    color: #fff;
}

.section-team .single-person:hover .person-image {
    padding: 12px;
    border: 4px dashed #fff;
}

.section-team .single-person .person-image img {
    width: 100%;
    border-radius: 50%;
}

.section-team .single-person .person-image .icon {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%);
    display: inline-block;
    width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    background: linear-gradient(to right, #016cec, #00b5f7);
    color: #fff;
    border-radius: 50%;
    font-size: 24px;
    background: none;
    background-color: #fff;
    color: #016cec;
}

.section-team .single-person:hover .person-image .icon {
    background: none;
    background-color: #fff;
    color: #016cec;
}

.section-team .single-person .person-info .full-name {
    margin-bottom: 10px;
    font-size: 28px;
    font-weight: 700;
    color: #fff;
}

.section-team .single-person .person-info .speciality {
    text-transform: uppercase;
    font-size: 14px;
    color: #016cec;
    color: #fff;
}

.section-team .single-person:hover .full-name,
.section-team .single-person:hover .speciality {
    color: #fff;
}
body {
    background-image: url("https://cdn.pixabay.com/photo/2019/09/24/16/05/storm-4501645_1280.jpg");
    background-repeat: no-repeat, repeat;
    background-position: center;
    /* Center the image */
    background-size: cover;
    /* Resize the background image to cover the entire container */
    padding-top: 50px;
}

.login-form {
    background: #1a11117a;
    margin-top: 100px;
    margin-bottom: 100px;
    padding: 100px;
    border-radius: 50px;
    color: white;
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

.login-heading {
    text-align: center;
    margin-top: 20px;
}

.container {
    background: #09090a4f;
    border-radius: 50px;
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.38);
}

.btn-primary {
    width: 100%;
}


/* Image */

.imagePreview {
    width: 100%;
    height: 180px;
    background-position: center center;
    background: url(http://cliquecities.com/assets/no-image-e3699ae23f866f6cbdf8ba2443ee5c4e.jpg);
    background-color: #fff;
    background-size: cover;
    background-repeat: no-repeat;
    display: inline-block;
    box-shadow: 0px -3px 6px 2px rgba(0, 0, 0, 0.2);
}

.btn-primary {
    display: block;
    border-radius: 0px;
    box-shadow: 0px 4px 6px 2px rgba(0, 0, 0, 0.2);
    margin-top: -5px;
}

.imgUp {
    margin-bottom: 15px;
}


/* Image Upload */

.uploadInput {
    padding: 10px;
    border: 1px solid #ccc;
    background: linear-gradient(135deg, #ffa000 20%, #ff5000 80%);
    color: #fff;
    max-width: 500px;
    box-shadow: 2px 2px 5px 2px hsla(0, 0%, 0%, 0.15);
}

.imagePreviewContainer {
    display: flex;
    padding: 10px;
    flex: 1 1 100vw;
    flex-flow: row wrap;
}

.previewItem {
    display: flex;
    margin: 10px;
    padding: 10px;
    flex: 0 0 350px;
    background: #fff;
    box-shadow: 1px 2px 5px 0px hsla(0, 0%, 0%, 0.35);
}

.imagePreview {
    max-width: 150px;
    height: 150px;
}

.details {
    padding: 10px;
}

.details h6 {
    font-size: 10pt;
    fong-weight: normal;
    color: #999;
    padding: 5px;
    margin: 0;
}

.details i {
    cursor: pointer;
}

.dragDrop {
    margin-top: 10px;
    height: 100px;
    background: hsla(0, 0%, 100%, 0.25);
    padding: 10px;
    text-align: center;
    text-transform: uppercase;
    color: #ddd;
    border: 2px dashed #eee;
}

.dragging {
    background-color: #fff;
    color: lightsteelblue;
}

.dragging i.material-icons {
    animation: bounce 1s infinite ease-in-out;
}

.inside {
    margin-top: 35px;
}

@keyframes bounce {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(5px);
    }
    100% {
        transform: translateY(0);
    }
}
